const Wrapper = styled.div`
	display: flex;
	flex-direction: column;
	padding: 21px 105px 0 76px;
`;

const TimeList = styled.ul`
	display: flex;
	gap: 20px;

	& li {
		font-weight: 400;
		line-height: 21px;
		color: #a3b2c3;
		cursor: pointer;

		&.active {
			color: black;
		}
	}
`;

const MainWrapper = styled.div`
	display: flex;
	margin-top: 14px;
	gap: 35px;
`;

const CalendarWrapper = styled.div`
	display: flex;
	flex-direction: column;
	flex: 1;
`;

const CalendarHeader = styled.header`
	display: flex;
	align-items: center;
	justify-content: space-between;
`;

const CalendarDateHeader = styled.div`
	margin-top: 46px;
	display: flex;
	align-items: center;
	justify-content: space-around;

	& p {
		font-size: 17px;
		font-weight: 400;
		line-height: 21px;
		color: #d1d3dc;

		&:first-child {
			color: #ff9797;
		}

		&:last-child {
			color: #85a0ff;
		}
	}
`;

const CalendarBody = styled.div`
	margin-top: 33px;

	& div {
		display: grid;
		grid-template-columns: repeat(7, 1fr);

		& div {
			position: relative;
			width: 100%;
			height: 0;
			padding-top: 81.04%;
			background-color: white;
			border: 1px solid #c9ccd7;

			& p {
				position: absolute;
				left: 8px;
				top: 9px;
				font-size: 14px;
				line-height: 17px;
				font-weight: 400;
				color: black;
			}
		}
		&:not(:first-child) div {
			border-top: none;
		}
	}
`;

const CalendarOptionsWrapper = styled.ul`
	display: flex;
	gap: 14px;
`;

const UserLists = styled.ul`
	display: flex;
	flex-direction: row-reverse;
	justify-content: flex-start;
	align-items: center;
	margin-right: 6px;

	li {
		width: 26px;
		height: 26px;
		border-radius: 50%;
		border: 1px solid #60c8c8;
		margin-right: -6px;
		background-color: #eff0f4;
		z-index: 1;

		span {
			${blind}
		}
	}
`;

const ScheduleWrapper = styled.div`
	display: flex;
	flex-direction: column;
`;

const ScheduleOptionList = styled.ul`
	display: table;
	border-collapse: collapse;
	height: 33px;
	cursor: pointer;

	& li {
		display: table-cell;
		text-align: center;
		vertical-align: middle;

		box-sizing: border-box;
		width: 136px;
		background-color: white;
		color: #30374f;
		border: 1px solid #c9ccd7;
		&.selected {
			background-color: #c9ccd7;
			color: white;
			/* border: none; */
		}
	}
`;

const ScheduleControlWrapper = styled.div`
	display: flex;
	margin-top: 41px;
	align-items: flex-start;
	justify-content: space-between;

	& div.textWrapper {
		display: flex;
	}

	& p.title {
		font-size: 20px;
		font-weight: 600;
		line-height: 24px;
		color: #313131;
	}

	& p.desc {
		font-size: 12px;
		font-weight: 400;
		line-height: 24px;
		color: #2f2f2f;
		margin-left: 13px;
	}

	& div.buttonWrapper {
		display: flex;
		flex-direction: column;
		gap: 27px;
	}
`;

const EmptySheduleList = styled.div`
	display: flex;
	justify-content: center;
	text-align: center;
	line-height: 17px;
	font-size: 14px;
	color: #30374f;
	align-items: center;
	border: 1px solid #c9ccd7;
	height: 186px;
	margin-top: 27px;
	font-weight: 500;
`;

const ScheduleList = styled.ul`
	display: flex;
	flex-direction: column;
	margin-top: 15px;
	gap: 13px;

	& li {
		display: flex;
		flex: 1;
		justify-content: space-between;
		align-items: center;
		padding: 15px 30px 14px 25px;
		border: 1px solid #c9ccd7;

		& div {
			display: flex;
			flex-direction: column;
			font-size: 14px;
			line-height: 17px;
			font-weight: 500;
			color: #30374f;
		}

		& .arrow {
			width: 8px;
			height: 8px;
			border: solid #30374f;
			border-width: 0 1.5px 1.5px 0;
			transform: rotate(45deg) translateX(-4px);
			transition: transform 0.3s ease;

			&.active {
				transform: rotate(225deg);
			}
		}
	}
`;

const PopuoGroup = styled.div`
	/* position: absolute; */
	padding: 21px 20px 22px;
	background: #ffffff;
	border: 1px solid #c9ccd7;
	box-shadow: 2px 4px 8px #e7e9f0;
	display: flex;
	flex-direction: column;
	font-size: 14px;
	font-weight: 500;
	line-height: 17px;
	width: 421px;

	span.title {
		color: #30374f;
	}

	div.code {
		display: flex;
		gap: 7px;
		margin-top: 16px;
		align-items: center;
		input {
			height: 39px;
			flex: 1;
			box-sizing: border-box;
			padding-left: 10px;
			background-color: #f4f6fc;
			border: none;

			&::placeholder {
				color: #9ca0ab;
			}
		}
	}

	div.link {
		display: flex;
		font-size: 12px;
		line-height: 15px;
		align-items: center;
		margin-top: 13px;
		border: 1px solid #ebedf3;
		padding: 8px 10px;

		span {
			color: #9ca0ab;
			flex: 1;
		}
	}
`;

export default Main;
